.error--section {
  width: 95%;
  max-width: 840px;
  margin: 10em auto 0;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px 0;
}
.error--section h1 {
  font-size: 10rem;
  font-family: var(--tertiary-font);
  margin-bottom: 2.5rem;
  color: var(--h1-color);
}
.error--section h2 {
  font-size: 2rem;
  font-weight: 500;
  text-transform: uppercase;
  text-shadow: 0 0 2px rgba(0, 0, 0, 0.25);
  margin-top: 1rem;
  color: var(--h1-color);
  font-family: var(--secondary-font);
}
.error--section p {
  color: #ff2b00;
}